import throttle from "lodash.throttle";
import "../css/03-feedback.css";
import "../css/common.css";

const STORAGE_KEY = "inputLocallStorageKey";

const feedback = {
  form: document.querySelector(".feedback-form"),
  input: document.querySelector(".feedback-form input"),
  textArea: document.querySelector(".feedback-form textarea"),
  formData: {},
  onFormSubmit(event) {
    event.preventDefault();
    // —Å—Ç–æ–ø —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω–æ–º—É –¥–µ–π—Å—Ç–≤–∏—é (–ø–µ—Ä–µ–∑–∞–≥—Ä—É–∑–∫–∞)
    console.log("–ú—ã —Å–æ–±—Ä–∞–ª–∏ –¥–∞–Ω–Ω—ã–µ");
    event.target.reset();
    //—Å–±—Ä–æ—Å –≤—Å–µ—Ö –ø–æ–ª–µ–π
  },
  onFormInput(event) {
    console.log(event.target.name);
    console.log(event.target.value);

    feedback.formData[event.target.name] = event.target.value;
    // –≤ —Ñ–æ—Ä–º –¥–∞—Ç—É –∑–∞–ø–∏—Å—ã–≤–∞–µ–º {email: "value", message: "value"}
    // console.log((feedback.formData[event.target.name] = event.target.value));

    localStorage.setItem(STORAGE_KEY, JSON.stringify(feedback.formData));
    // –ø–µ—Ä–µ–¥–µ–ª—ã–≤–∞–µ–º –≤ JSON {email: "value", message: "value"} ==> {"email": "value", "message": "value"} –∏ –∑–∞–ø–∏—Å—ã–≤–∞–µ–º –≤ –ª–æ–∫–∞–ª—å–Ω–æ–µ —Ö—Ä–∞–Ω–∏–ª–∏—â–µ STORAGE_KEY
  },
  populateTextareaMulti() {
    const message = JSON.parse(localStorage.getItem(STORAGE_KEY));
    // –≤–æ–∑–≤—Ä–∞—â–∞–µ–º JSON –≤ –æ–±—ã—á–Ω—ã–π –æ–±—ä–µ–∫—Ç –∏ –∑–∞–ø–∏—Å—ã–≤–∞–µ–º –≤ –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é

    if (!message) {
      return;
    }
    // –µ—Å–ª–∏ –≤ localStorage –Ω–µ—Ç –∏–Ω—Ç–∏—Ä–∏—Å—É—é—â–µ–π –Ω–∞—Å –∑–∞–ø–∏—Å–∏ —Ç–æ –∑–∞–∫–∞–Ω—á–∏–≤–∞–µ–º —Ñ—É–Ω–∫—Ü–∏—é
    feedback.textArea.value = message.message;
    // –≤–Ω–æ—Å–∏–º –¥–∞–Ω–Ω—ã–µ –≤ –ø–æ–ª–µ –≤–≤–æ–¥–∞ –∏–∑ message: "value"
    feedback.input.value = message.email;
    // –≤–Ω–æ—Å–∏–º –¥–∞–Ω–Ω—ã–µ –≤ –ø–æ–ª–µ –≤–≤–æ–¥–∞ –∏–∑ email: "value"
  },
};

feedback.populateTextareaMulti();
// –∑–∞–ø—É—Å–∫–∞–µ–º —Ñ—É–Ω–∫—Ü–∏—é –∫–æ—Ç–æ—Ä–∞—è –∑–∞–ø–æ–ª–Ω—è–µ—Ç –ø–æ–ª—è –∏–∑ localStorage
feedback.form.addEventListener("submit", feedback.onFormSubmit);
// –∑–∞–ø—É—Å–∫–∞–µ–º —Å–ª—É—à–∞—Ç–µ–ª—è —Å–æ–±—ã—Ç–∏–π –Ω–∞ –æ—Ç–ø—Ä–∞–≤–∫—É —Ñ–æ—Ä–º—ã + –∫–æ–ª–±–µ–∫ —Ñ—É–Ω–∫—Ü–∏—é
feedback.form.addEventListener("input", throttle(feedback.onFormInput, 500));
// –∑–∞–ø—É—Å–∫–∞–µ–º —Å–ª—É—à–∞—Ç–µ–ª—è —Å–æ–±—ã—Ç–∏–π –Ω–∞ –∑–∞–ø–æ–ª–Ω–µ–Ω–∏–µ —Ñ–æ—Ä–º + –∫–æ–ª–±–µ–∫ —Ñ—É–Ω–∫—Ü–∏—é + lodash.throttle (–∑–∞–¥–µ—Ä–∂–∫–∞ 500 —Å–µ–∫—É–Ω–¥)
